@media screen and (max-width: 750px){
  #taskbar {
    bottom: 0;


    ul {
      width: 100%;
      padding: calc(10px + 2vw) 0;
      border-radius: 0;
      justify-content: space-evenly;

      li {
        margin: 0;
      }
    }
  }

  #home {
    content > div > * {
      width: 85%;
    }
  }

  .tooltip {
    display: none;
  }


}
